<template>
  <div class="container">
  <h2>產品名稱：{{ product.title }}</h2>
    <div class="row">
      <img :src="product.imageUrl" alt="">
      其他資料：{{ product }}
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      product: {}
    }
  },
  methods: {
    getProduct () {
      const { id } = this.$route.params
      this.$http(`${process.env.VUE_APP_API}/v2/api/${process.env.VUE_APP_PATH}/product/${id}`)
        .then(res => {
          console.log(res)
          this.product = res.data.product
        })
        .catch(err => {
          console.log(err)
        })
    }
  },
  mounted () {
    this.getProduct()
  }
}
</script>
